.inputForm
{
    background-color: rgb(241, 8, 144);
    width: 350px;
    margin: 0 auto;
    box-shadow: -1px 6px 46px 0px pink;
    margin-top: 50px;
    padding: 20px;
    border-radius: 5px;
    margin-bottom: 40px;
    color: white;
}
.inputForm input,textarea
{
    width: 300px;
    border: 1px solid white;
    padding: 10px 10px;
    outline: none;
    margin-bottom: 20px;
    font-size: 16px;
    color: green;
}
button
{
    background-color:green;
    color: white;
    font-weight: bold;
    margin-left: 120px;
    padding: 10px;
    border-radius: 10px;
    cursor: pointer;
}
.h1
{
    margin-left: 300px;
}